import ws from "../http/client/base/ws"
import { websocket } from "../http/client/websocket"
const mimeHandler = require('../http/interceptor/mimeHandler')
const errorcodeHandler = require('../http/interceptor/errorcodeHandler')
const timeoutHandler = require('../http/interceptor/timeoutHandler')
const interceptor = require('../http/interceptor');

const wsClient = _ => {
    ws.setPlatformDefaultClient(websocket)
    return ws
}

export {
    wsClient,
    timeoutHandler,
    interceptor
}


