/**
 * WebSocket连接器 具体实现 （实现了重连机制）
 * Created by dfzq on 2017/8/23.
 */
(function (define, global) {
    'use strict';

    define(function (require) {

        var responsePromise, client, eventBus;

        client = require('./base/client')
        responsePromise = require('../util/responsePromise')
        eventBus = require('./eventBus')

        /// Private state variables

        // // EventBus实例，基于sock.js和vert.x (修改为连接池模式，因此这里被取消了)
        // var eb = null
        // 待发送和注册Handler
        var toBeHandlers = []
        // EventBus连接池
        var ebPool = []

        // 超时间隔时间，精确到毫秒
        var timeoutInterval = 3000
        // 重新连接的间隔时间，精确到毫秒
        var reconnectInterval = 1000
        // 重新连接的最大间隔时间，精确到毫秒
        var maxReconnectInterval = 30000
        // 重新连接延迟的增长率。当允许连接尝试重连时问题依然存在时
        var reconnectDecay = 1.5

        // /**
        //  * 构建一个自定义事件
        //  * @param s 事件名
        //  * @param args  事件参数
        //  */
        // function generateEvent(s, args) {
        //     var evt = document.createEvent('CustomEvent')
        //     evt.initCustomEvent(s, false, false, args)
        //     return evt
        // }

        /**
         * 建立websocket通讯层
         */
        function setupEventBus(request) {
            // 引入websocket连接池思想
            let existEbInstance = ebPool.find(ele => ele.connectionPath.indexOf(request.path) != -1)

            let eb
            if (!existEbInstance){
                eb = new eventBus(request.path)

                // 将新创建的eb实例放入连接池内
                let ebInstance = {
                    connectionPath: request.path,
                    ebInstance: eb,
                    // 重连次数
                    reconnectAttempt: 0,
                    // 最大重连次数
                    maxReconnectAttempt: null,
                    // 是否超时标识符
                    timedOut: false,
                    // 当前连接状态
                    readyState: WebSocket.CONNECTING,
                }
                ebPool.push(ebInstance)

                // 当前websocket实例
                let localWs = eb

                // 设置超时处理器
                let timeout = setTimeout(function () {
                    ebInstance.timedOut = true
                    localWs.close()
                    ebInstance.timedOut = false
                }, timeoutInterval)

                eb.onopen = function (){
                    // 清除超时处理器
                    clearTimeout(timeout)
                    console.info('WebSocket[' + eb.sockJSConn.url + '][onopen]')

                    // 重置相关属性
                    ebInstance.readyState = WebSocket.OPEN
                    ebInstance.reconnectAttempt = 0

                    // 一旦链接被成功建立后，发送暂存toBeHandlers
                    if (toBeHandlers.length > 0){

                        toBeHandlers.forEach(function (handler) {
                            if (eb.sockJSConn.url.indexOf(handler.gateway) != -1 && !handler.fulfill) {
                                if (handler.type == 'send'){
                                    eb.send(handler.channel, handler.body, handler.headers, handler.replyHandler)
                                    handler.fulfill = true
                                }

                                // 实现双工回调的函数注册
                                if (handler.type == 'register'){
                                    eb.registerHandler(handler.channel, handler.headers, handler.replyHandler)
                                    handler.fulfill = true
                                }
                            }
                        })

                    }

                }

                // 重发机制
                eb.onclose = function() {
                    console.info('WebSocket[' + eb.sockJSConn.url + '][onclose]')
                    let existEbInstanceIndex = ebPool.findIndex(ele => ele.connectionPath.indexOf(request.path) != -1)
                    // 找到了对应的索引值，进行删除
                    if (existEbInstanceIndex != -1) {
                        ebPool.splice(existEbInstanceIndex, 1)
                    }

                    // 清除超时处理器
                    clearTimeout(timeout)
                    eb = null

                    // 设置连接实例的属性
                    ebInstance.readyState = WebSocket.CONNECTING

                    // 根据递减算法计算重连连接的延时增长时间
                    let timeout = reconnectInterval * Math.pow(reconnectDecay, ebInstance.reconnectAttempt)
                    setTimeout(function () {
                        ebInstance.reconnectAttempt++
                        setupEventBus(request)
                    }, timeout > maxReconnectInterval ? maxReconnectInterval : timeout)
                }

            }
            else {
                eb = existEbInstance.ebInstance
            }

            return eb
        }

        let websocketClient = client(function websocket(request) {
            return responsePromise.promise(function (resolve, reject) {

                // 去除url上的get和\参数
                if (request.path.indexOf('?') != -1) {
                    request.path = request.path.substring(0, request.path.indexOf('?'))
                }
                request.path = request.path.replace(/\/ /,'').trim()

                // 建立ws通道
                let eb = setupEventBus(request)

                let response = { request: request }

                // 扩展消息头
                let headers = {}

                // 消息体JSON对象结构
                let msgBody = JSON.parse(request.entity)

                // 当websocket正在建立链接时，有toBeSendHandler进行注册则进行暂存，待链接被open时才进行调用
                if (eb.state == eventBus.CONNECTING) {
                    // 暂存Handler中的待发送消息体结构
                    let envelope = {
                        gateway: request.path,      // 对应的网关地址
                        type: 'send',
                        channel: request.channel,
                        headers: headers,
                        body: msgBody,
                        replyHandler: callback,
                    }

                    toBeHandlers.push(envelope)
                }
                else{
                    eb.send(request.channel, msgBody, headers, callback)
                }

                function callback(err, rec) {
                    // 框架级别异常处理
                    if (err) {
                        // 通讯层发生超时
                        if (err.failureType == 'TIMEOUT') {
                            resolve({
                                entity: {
                                    code: 7100,
                                },
                            })
                        }
                        // 其他未知异常
                        else{
                            resolve({
                                entity: {
                                    code: 99999,
                                },
                            })
                        }
                    }
                    else {
                        response.address = rec.address
                        response.type = rec.type
                        response.entity = rec.body
                        resolve(response)
                    }
                }

            })
        })

        /**
         * 对当前连接实例进行函数扩展
         * @param impl 当前连接实例
         */
        const mixedEb = function (impl) {

            /**
             * 双工订阅，可接受服务端的数据响应
             * @param address   服务地址
             * @param headers   消息头
             * @param callback  回调函数
             */
            impl.register = function (address, headers, callback) {

                if (typeof headers === 'function') {
                    callback = headers;
                    headers = {};
                }

                if (impl.state == eventBus.CONNECTING) {
                    // 暂存Handler中的待发送消息体结构
                    let envelope = {
                        gateway: impl.sockJSConn.url,
                        type: 'register',
                        channel: address,
                        headers: headers,
                        body: null,
                        replyHandler: callback,
                    }

                    toBeHandlers.push(envelope)
                }
                else{
                    impl.registerHandler(address, headers, callback)
                }

            }

            /**
             * 双工移除订阅
             * @param address   服务地址
             * @param headers   消息头
             * @param callback  回调函数
             */
            impl.unregister = function (address, headers, callback) {
                impl.unregisterHandler(address, headers, callback)
            }

            return impl
        }

        /**
         * 从连接池中获取对应网关的连接实例
         * 如果连接池中没有则会自动构建，如果存在则直接取该已创建的连接实例
         * @param path  网关绝对路径
         * @returns {Array}
         */
        websocketClient.getInstanceFromConnectionPool = function (path) {
            if (!path && !(path.startsWith('http:') || path.startsWith('https:'))){
                throw new Error('param 0 of getInstanceFromConnectionPool is required.')
            }

            // 去除末尾空格和/
            if (path.indexOf('?') != -1) {
                path = path.substring(0, path.indexOf('?'))
            }
            path = path.replace(/\/ /,'').trim()
            let existEbInstance = ebPool.find(ele => ele.connectionPath.indexOf(path) != -1)

            let currentEb
            if (!existEbInstance) {
                // 自动构建该连接实例
                currentEb = setupEventBus({
                    path: path
                })
            } else {
                // 当前连接实例
                currentEb = existEbInstance.ebInstance
            }

            return mixedEb(currentEb)
        }

        return {
            websocket: websocketClient
        }

    });

}(
    typeof define === 'function' && define.amd ? define : function (factory) {
        module.exports = factory(require);
    },
    typeof window !== 'undefined' ? window : void 0
    // Boilerplate for AMD and Node
));


